@charset "UTF-8";
:root {
  --speed: 300ms;
  --time-function: ease-in-out;
  --vh: 1vh;
}

/*images*/
/*=======================START-MEDIA=====================*/
/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
h1, .h1-title {
  font-size: 36px;
  line-height: 1.33;
}

@media only screen and (min-width: 1200px) {
  h1, .h1-title {
    font-size: 70px;
  }
}

h2, .h2-title {
  font-size: 32px;
  line-height: 1.15;
}

@media only screen and (min-width: 1200px) {
  h2, .h2-title {
    font-size: 52px;
  }
}

h3, .h3-title {
  font-size: 28px;
  line-height: 1.4;
}

@media only screen and (min-width: 1200px) {
  h3, .h3-title {
    font-size: 30px;
  }
}

h4, .h4-title {
  font-size: 20px;
  line-height: 1.5;
}

@media only screen and (min-width: 1200px) {
  h4, .h4-title {
    font-size: 22px;
  }
}

h5, .h5-title {
  font-size: 20px;
  font-size: calc(16px + (20 - 16) * ((100vw - 480px) / (1920 - 480)));
  line-height: 22px;
}

@media (max-width: 480px) {
  h5, .h5-title {
    font-size: 16px;
  }
}

@media (min-width: 1920px) {
  h5, .h5-title {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1200px) {
  h5, .h5-title {
    line-height: 24px;
  }
}

h6, .h6-title {
  font-size: 16px;
  font-size: calc(14px + (16 - 14) * ((100vw - 480px) / (1920 - 480)));
  line-height: 20px;
}

@media (max-width: 480px) {
  h6, .h6-title {
    font-size: 14px;
  }
}

@media (min-width: 1920px) {
  h6, .h6-title {
    font-size: 16px;
  }
}

/*--------- font-face ---------*/
@font-face {
  font-family: DMSans;
  font-display: swap;
  src: url("../fonts/DMSans/DMSans-Regular.woff") format("woff"), url("../fonts/DMSans/DMSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  text-decoration-skip: objects;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  min-width: 320px;
  overflow-x: hidden;
}

html {
  box-sizing: border-box;
}

html, body {
  min-height: 100%;
}

html, body {
  height: 100%;
}

audio,
video {
  display: block;
}

section {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:focus {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a img {
  border: none;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul, li {
  list-style-type: none;
}

img {
  max-width: 100%;
  display: block;
  border-style: none;
}

input,
textarea,
select {
  vertical-align: middle;
}

form, fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
  width: 100%;
}

input[type="search"],
input[type="text"],
input[type="file"],
input[type="password"],
input[type="submit"],
input[type="email"],
input[type="tel"],
textarea, select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  resize: none;
  outline: none;
}

input[type="email"] {
  box-shadow: none;
  -webkit-box-shadow: none;
}

input[type="image"] {
  padding: 0;
  border: none;
}

input[type="submit"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input[type="submit"],
input[type="button"] {
  cursor: pointer;
}

input[type="submit"]:focus,
input[type="button"]:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button,
input[type=email]::-webkit-inner-spin-button,
input[type=email]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

input::placeholder, input::-moz-placeholde, input::-webkit-input-placeholder {
  opacity: 0.5;
  color: inherit;
}

button:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

button {
  cursor: pointer;
  outline: none;
  border: none;
  box-shadow: none;
}

p, span, label, h1, h2, h3, h4, h5, h6, b, strong, i, em {
  max-width: 100%;
  word-wrap: break-word;
  white-space: normal;
  margin: 0;
}

div {
  word-wrap: break-word;
  white-space: normal;
  box-sizing: border-box;
}

iframe {
  border: none;
}

input::-ms-clear {
  display: none;
}

button,
input,
optgroup,
select,
textarea,
pre {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

body {
  font-size: 18px;
  box-sizing: border-box;
  background-color: #fff;
  background-position: top center;
  background-size: cover;
}

span, a, p, strong, em, b {
  line-height: 1.5;
}

a {
  color: inherit;
}

.container {
  max-width: 1682px;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}

*:focus, *:focus-visible {
  outline: none !important;
}

h1, h2, h3, h4, h4, h6, .h1-title, .h2-title, .h3-title, .h4-title, .h5-title, .h6-title {
  display: block;
  font-weight: 700;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.main {
  flex: 1 0 auto;
}

.btn-full-reset {
  padding: 0;
  border: 0;
  background-color: transparent;
}

.hidden {
  display: none !important;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.disable {
  pointer-events: none !important;
}

.icon {
  display: inline-block;
  fill: currentColor;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-search:before {
  content: "";
}

.icon-false:before {
  content: "";
}

.icon-arrow-left:before {
  content: "";
}

.icon-arrow-right:before {
  content: "";
}

.icon-twitter:before {
  content: "";
}

.icon-pinterest:before {
  content: "";
}

.icon-facebook:before {
  content: "";
}

.icon-instagram:before {
  content: "";
}

.icon-linkedin:before {
  content: "";
}

.btn {
  background: #ffa900;
}

.btn, .btn:focus {
  border: 1px solid #ffa900;
}

@media (any-hover: hover) {
  .btn:hover {
    background: rgba(255, 255, 255, 0);
  }
}

.menu {
  display: flex;
  align-items: center;
}
